/* eslint-disable */
import React from 'react'
import {graphql} from 'gatsby'
import SEO from '../components/SEO'
import get from 'lodash/get'
import ProductSummary from '../components/ProductSummary'
import ProductAttributes from '../components/ProductAttributes'
import Layout from '../components/Layout'

class ProductPageTemplate extends React.PureComponent {
  render() {
    const productInfo = get(this, 'props.data.allSitePage')
    const data = productInfo.nodes[0].context
    const slug = data.name
    const product = {
      ...data,
      id: data.id,
      image: data.image,
      header: data.name,
      sku: data.sku,
      price: data.price,
      description: data.description,
    }

    // if (!sizes) return null

    return (
      <Layout location={this.props.location}>
        <SEO title={slug} />
        <ProductSummary {...product} />
        <ProductAttributes {...product} />
      </Layout>
    )
  }
}

export default ProductPageTemplate

export const pageQuery = graphql`
query ProductsQuery($id: String!) {
  allSitePage(filter: {context: {id: {eq: $id}}}) {
    nodes {
      context {
        id,
        name,
        image,
        price,
        sku,
        description,
      }
    }
  }
}
`
